import React, { useEffect, useState } from 'react';
import { Button, Container, Row, Table } from 'reactstrap';
import { Repe } from '../Repetition/Repe';

export const Serie = ({ pNumSeries, reps, editable, size, pWeigth }) => {
  const [numSeries, setNumSeries] = useState(Array.from(Array(reps).keys()));
  const [isEditable, setIsEditable] = useState(editable);
  const [weight, setWeigth] = useState(pWeigth);
  const [numRepes, setNumRepes] = useState(pWeigth);
  const [title, setTitle] = useState(size);

  useEffect(() => {
    setIsEditable(editable);
    setRepes(Array.from(Array(reps).keys()));
    setWeigth(pWeigth);
  }, [editable, reps, pWeigth, size]);

  const handleOnSave = () => {
    alert('Saved!!');
  };

  return (
    <Container>
      <Row>
        <h2>This is a {title} size serie</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>#repetitions</th>
              <th>weight</th>
            </tr>
          </thead>
          <tbody>
            {repes.map(r => (
              <Repe key={r} id={r} pNum={3} pWeigth={weight} editable={editable}></Repe>
            ))}
          </tbody>
        </Table>
        <Button onClick={handleOnSave}>Save</Button>
      </Row>
    </Container>
  );
};
